@include table-row-variant('alpha', $state-alpha-bg);
@include table-row-variant('bravo', $state-bravo-bg);
@include table-row-variant('charlie', $state-charlie-bg);
@include table-row-variant('delta', $state-delta-bg);
@include table-row-variant('echo', $state-echo-bg);
@include table-row-variant('foxtrot', $state-foxtrot-bg);
@include table-row-variant('golf', $state-golf-bg);


.table-alpha {
  @include table-head-variant($brand-alpha, #fff, inherit);
}
.table-bravo {
  @include table-head-variant($brand-bravo, #fff, inherit);
}
.table-charlie {
  @include table-head-variant($brand-charlie, #fff, inherit);
}
.table-delta {
  @include table-head-variant($brand-delta, #fff, inherit);
}
.table-echo {
  @include table-head-variant($brand-echo, #fff, inherit);
}
.table-foxtrot {
  @include table-head-variant($brand-foxtrot, #fff, inherit);
}
.table-golf {
  @include table-head-variant($brand-golf, #fff, inherit);
}


.table-alpha-light {
  @include table-head-variant($state-alpha-bg, $state-alpha-text, $state-alpha-border);
}
.table-bravo-light {
  @include table-head-variant($state-bravo-bg, $state-bravo-text, $state-bravo-border);
}
.table-charlie-light {
  @include table-head-variant($state-charlie-bg, $state-charlie-text, $state-charlie-border);
}
.table-delta.table-light {
  @include table-head-variant($state-delta-bg, $state-delta-text, $state-delta-border);
}
.table-echo.table-light {
  @include table-head-variant($state-echo-bg, $state-echo-text, $state-echo-border);
}
.table-foxtrot.table-light {
  @include table-head-variant($state-foxtrot-bg, $state-foxtrot-text, $state-foxtrot-border);
}
.table-golf.table-light {
  @include table-head-variant($state-golf-bg, $state-golf-text, $state-golf-border);
}


.table-hidden-row-striped > tbody > tr:nth-of-type(4n) {
  background-color: #f9f9f9;
}
