#page-wrapper {
  padding-left: 85px;
  padding-right: 15px;
  padding-top: 90px;
  transition: all .7s ease 0s;
}
#page-wrapper.open {
  padding-left: 275px;
}
.navbar-fixed-top {
  left: 0;
  padding-left: 75px;
  height: 60px;
  transition: all .7s ease 0s;
}
#page-wrapper.open .navbar-fixed-top {
  left: 250px;
  padding-left: 0;
}
#page-wrapper.open #sidebar-wrapper {
  left: 150px;
}
#sidebar-wrapper {
  background: #f8f8f8;
  border-right: 1px solid #e7e7e7;
  margin-left: -150px;
  top: 0;
  left: -30px;
  width: 250px;
  position: fixed;
  height: 100%;
  transition: all .7s ease 0s;
  z-index: 1031;
}
.sidebar-main {
  height: 65px;
}
.sidebar-main .menu-icon {
  float: right;
  font-size: 22px;
  padding-right: 26px;
  line-height: 59px;
}
.sidebar-main .menu-img {
  float: right;

}
#page-wrapper.open .sidebar-main .menu-img {
  padding: 13px 18px;
  transition: all .7s ease;
  transform: rotate(360deg);
}
#page-wrapper:not(.open) .sidebar-main .menu-img {
  padding: 13px 18px;
  transition: all .7s ease;
  transform: rotate(-360deg);
}
.sidebar-list {
  height: 40px;
}
.sidebar-list .menu-icon.stacked {
  display: inline-block;
  width: 54px;
  margin-right: 19px;
}
.sidebar-list .menu-icon.stacked i {
  padding-top: 6px;
}
.sidebar-list .menu-icon.stacked span {
  font-size: 13px;
  color: #fff;
}
#page-wrapper.open .navbar .navbar-brand {
  margin-left: -118px;;
  transition: all .7s ease 0s;
}
#page-wrapper:not(.open) .navbar .navbar-brand {
  margin-left: -18px;
  transition: all .7s ease 0s;
}
.sidebar-list .menu-icon,
.sidebar-list .menu-icon.stacked {
  float: right;
  line-height: 40px;
}
.sidebar-list .menu-icon {
  width: 70px;
  text-align: center;
  padding-right: 20px;
}
.sidebar-list a:hover .menu-icon {
  text-indent: 20px;
}
.sidebar-list a:hover .menu-icon.stacked {
  text-indent: 20px;
}
