//== Colors
$brand-alpha:                     #db3694;
$brand-bravo:                     #673075;
$brand-charlie:                   #ff7900;
$brand-delta:                     #3897c9;
$brand-echo:                      #5cb85c;
$brand-foxtrot:                   #ffc400;
$brand-golf:                      #e42525;


//== Typography
@font-face {
  font-family: 'SourceSansPro';
  src: url('../fonts/SourceSansPro-Bold.eot');
  src: url('../fonts/SourceSansPro-Bold.woff') format('woff'), url('../fonts/SourceSansPro-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: 'SourceSansPro';
  src: url('../fonts/SourceSansPro-Light.eot');
  src: url('../fonts/SourceSansPro-Light.woff') format('woff'), url('../fonts/SourceSansPro-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'SourceSansPro';
  src: url('../fonts/SourceSansPro-Regular.eot');
  src: url('../fonts/SourceSansPro-Regular.woff') format('woff'), url('../fonts/SourceSansPro-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'SourceSerifPro';
  src: url('../fonts/SourceSerifPro-Regular.eot');
  src: url('../fonts/SourceSerifPro-Regular.woff') format('woff'), url('../fonts/SourceSerifPro-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Medecision';
  src: url('../fonts/Medecision.eot');
  src: url('../fonts/Medecision.woff') format('woff'), url('../fonts/Medecision.ttf') format('truetype'), url('../fonts/Medecision.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

$font-family-sans-serif:          "Medecision", "Helvetica Neue", Helvetica, Arial, sans-serif;
$font-family-serif:               "SourceSerifPro", Georgia, "Times New Roman", Times, serif;
$font-family-monospace:           Menlo, Monaco, Consolas, "Courier New", monospace;
$font-family-base:                $font-family-sans-serif;


//== Buttons
$btn-alpha-color:                 #fff !default;
$btn-alpha-bg:                    $brand-alpha !default;
$btn-alpha-border:                darken($btn-alpha-bg, 5%) !default;

$btn-bravo-color:                 #fff !default;
$btn-bravo-bg:                    $brand-bravo !default;
$btn-bravo-border:                darken($btn-bravo-bg, 5%) !default;

$btn-charlie-color:               #fff !default;
$btn-charlie-bg:                  $brand-charlie !default;
$btn-charlie-border:              darken($btn-charlie-bg, 5%) !default;

$btn-delta-color:                 #fff !default;
$btn-delta-bg:                    $brand-delta !default;
$btn-delta-border:                darken($btn-delta-bg, 5%) !default;

$btn-echo-color:                  #fff !default;
$btn-echo-bg:                     $brand-echo !default;
$btn-echo-border:                 darken($btn-echo-bg, 5%) !default;

$btn-foxtrot-color:               #fff !default;
$btn-foxtrot-bg:                  $brand-foxtrot !default;
$btn-foxtrot-border:              darken($btn-foxtrot-bg, 5%) !default;

$btn-golf-color:                  #fff !default;
$btn-golf-bg:                     $brand-golf !default;
$btn-golf-border:                 darken($btn-golf-bg, 5%) !default;


//== Form states
$state-alpha-text:                $brand-alpha !default;
$state-alpha-bg:                  transparentize($brand-alpha, 0.8) !default;
$state-alpha-border:              transparentize($brand-alpha, 0.7) !default;

$state-bravo-text:                $brand-bravo !default;
$state-bravo-bg:                  transparentize($brand-bravo, 0.8) !default;
$state-bravo-border:              transparentize($brand-bravo, 0.7) !default;

$state-charlie-text:              $brand-charlie !default;
$state-charlie-bg:                transparentize($brand-charlie, 0.8) !default;
$state-charlie-border:            transparentize($brand-charlie, 0.7) !default;

$state-delta-text:                $brand-delta !default;
$state-delta-bg:                  transparentize($brand-delta, 0.8) !default;
$state-delta-border:              transparentize($brand-delta, 0.7) !default;

$state-echo-text:                 $brand-echo !default;
$state-echo-bg:                   transparentize($brand-echo, 0.8) !default;
$state-echo-border:               transparentize($brand-echo, 0.7) !default;

$state-foxtrot-text:              $brand-foxtrot !default;
$state-foxtrot-bg:                transparentize($brand-foxtrot, 0.8) !default;
$state-foxtrot-border:            transparentize($brand-foxtrot, 0.7) !default;

$state-golf-text:                 $brand-golf !default;
$state-golf-bg:                   transparentize($brand-golf, 0.8) !default;
$state-golf-border:               transparentize($brand-golf, 0.7) !default;


//== Alerts
$alert-alpha-bg:                  $state-alpha-bg !default;
$alert-alpha-text:                $state-alpha-text !default;
$alert-alpha-border:              $state-alpha-border !default;

$alert-bravo-bg:                  $state-bravo-bg !default;
$alert-bravo-text:                $state-bravo-text !default;
$alert-bravo-border:              $state-bravo-border !default;

$alert-charlie-bg:                $state-charlie-bg !default;
$alert-charlie-text:              $state-charlie-text !default;
$alert-charlie-border:            $state-charlie-border !default;

$alert-delta-bg:                  $state-delta-bg !default;
$alert-delta-text:                $state-delta-text !default;
$alert-delta-border:              $state-delta-border !default;

$alert-echo-bg:                   $state-echo-bg !default;
$alert-echo-text:                 $state-echo-text !default;
$alert-echo-border:               $state-echo-border !default;

$alert-foxtrot-bg:                $state-foxtrot-bg !default;
$alert-foxtrot-text:              $state-foxtrot-text !default;
$alert-foxtrot-border:            $state-foxtrot-border !default;

$alert-golf-bg:                   $state-golf-bg !default;
$alert-golf-text:                 $state-golf-text !default;
$alert-golf-border:               $state-golf-border !default;


//== Labels
$label-alpha-bg:                  $brand-alpha !default;
$label-bravo-bg:                  $brand-bravo !default;
$label-charlie-bg:                $brand-charlie !default;
$label-delta-bg:                  $brand-delta !default;
$label-echo-bg:                   $brand-echo !default;
$label-foxtrot-bg:                $brand-foxtrot !default;
$label-golf-bg:                   $brand-golf !default;


//== Progress bars
$progress-bar-alpha-bg:          $brand-alpha !default;
$progress-bar-bravo-bg:          $brand-bravo !default;
$progress-bar-charlie-bg:        $brand-charlie !default;
$progress-bar-delta-bg:          $brand-delta !default;
$progress-bar-echo-bg:           $brand-echo !default;
$progress-bar-foxtrot-bg:        $brand-foxtrot !default;
$progress-bar-golf-bg:           $brand-golf !default;


//== Panels
$panel-alpha-light-text:          $state-alpha-text !default;
$panel-alpha-light-border:        $state-alpha-border !default;
$panel-alpha-light-heading-bg:    $state-alpha-bg !default;

$panel-alpha-inverse-text:        #fff !default;
$panel-alpha-inverse-border:      $brand-alpha !default;
$panel-alpha-inverse-heading-bg:  $brand-alpha !default;

$panel-alpha-text:                $brand-alpha !default;
$panel-alpha-border:              #ddd !default;
$panel-alpha-heading-bg:          #f5f5f5 !default;

$panel-bravo-light-text:          $state-bravo-text !default;
$panel-bravo-light-border:        $state-bravo-border !default;
$panel-bravo-light-heading-bg:    $state-bravo-bg !default;

$panel-bravo-inverse-text:        #fff !default;
$panel-bravo-inverse-border:      $brand-bravo !default;
$panel-bravo-inverse-heading-bg:  $brand-bravo !default;

$panel-bravo-text:          	  $brand-bravo !default;
$panel-bravo-border:              #ddd !default;
$panel-bravo-heading-bg:          #f5f5f5 !default;

$panel-charlie-light-text:        $state-charlie-text !default;
$panel-charlie-light-border:      $state-charlie-border !default;
$panel-charlie-light-heading-bg:  $state-charlie-bg !default;

$panel-charlie-inverse-text:        #fff !default;
$panel-charlie-inverse-border:      $brand-charlie !default;
$panel-charlie-inverse-heading-bg:  $brand-charlie !default;

$panel-charlie-text:        	  $brand-charlie !default;
$panel-charlie-border:            #ddd !default;
$panel-charlie-heading-bg:        #f5f5f5 !default;

$panel-delta-light-text:          $state-delta-text !default;
$panel-delta-light-border:        $state-delta-border !default;
$panel-delta-light-heading-bg:    $state-delta-bg !default;

$panel-delta-inverse-text:        #fff !default;
$panel-delta-inverse-border:      $brand-delta !default;
$panel-delta-inverse-heading-bg:  $brand-delta !default;

$panel-delta-text:          	  $brand-delta !default;
$panel-delta-border:              #ddd !default;
$panel-delta-heading-bg:          #f5f5f5 !default;

$panel-echo-light-text:           $state-echo-text !default;
$panel-echo-light-border:         $state-echo-border !default;
$panel-echo-light-heading-bg:     $state-echo-bg !default;

$panel-echo-inverse-text:        #fff !default;
$panel-echo-inverse-border:      $brand-echo !default;
$panel-echo-inverse-heading-bg:  $brand-echo !default;

$panel-echo-text:          	      $brand-echo !default;
$panel-echo-border:               #ddd !default;
$panel-echo-heading-bg:           #f5f5f5 !default;

$panel-foxtrot-light-text:        $state-foxtrot-text !default;
$panel-foxtrot-light-border:      $state-foxtrot-border !default;
$panel-foxtrot-light-heading-bg:  $state-foxtrot-bg !default;

$panel-foxtrot-inverse-text:        #fff !default;
$panel-foxtrot-inverse-border:      $brand-foxtrot !default;
$panel-foxtrot-inverse-heading-bg:  $brand-foxtrot !default;

