$panel-golf-light-border:         $state-golf-border !default;
$panel-golf-light-heading-bg:     $state-golf-bg !default;

$panel-golf-inverse-text:        #fff !default;
$panel-golf-inverse-border:      $brand-golf !default;
$panel-golf-inverse-heading-bg:  $brand-golf !default;

$panel-golf-text:         	      $brand-golf !default;
$panel-golf-border:               #ddd !default;
$panel-golf-heading-bg:           #f5f5f5 !default;