// Sidebar

@mixin sidebar-variant($text-color) {
  #page-wrapper:not(.open) & .sidebar-title {
    display: none;
    height: 0;
    text-indent: -100px;
  }

  & {
    position: absolute;
    top: 0;
    bottom: 0;
    padding: 0;
    margin: 0;
    list-style: none;
    text-indent: 20px;
    overflow-x: hidden;
    overflow-y: auto;
  }
  & li a {
    color: #fff;
    display: block;
    float: left;
    text-decoration: none;
    width: 250px;
  }
  & .sidebar-main a {
    font-size: 19px;
    font-weight: bold;
    line-height: 59px;
    color: $text-color;
    display: block;
    float: left;
    text-decoration: none;
    width: 250px;
    border-bottom: 1px solid #e7e7e7;
    border-right: 1px solid #e7e7e7;
  }
  #page-wrapper:not(.open) & .sidebar-list a:hover .menu-icon {
    border-left: 4px solid $text-color;
    text-indent: 17px;
  }
  #page-wrapper:not(.open) & .sidebar-list a:hover .menu-icon.stacked {
    text-indent: 17px;
  }
  #page-wrapper:not(.open) & .sidebar-list a:hover .menu-icon.stacked span {
    text-indent: 15px;
  }
  & .sidebar-list a {
    font-size: 15px;
    color: $text-color;
    line-height: 40px;
  }
  & .sidebar-list a:hover {
    color: #fff;
    border-left: 4px solid $text-color;
    text-indent: 15px;
    background: #696969;
  }
  & .sidebar-list a:hover .stacked span {
    color: #696969;
  }
  & .divider {
    background-color: #e7e7e7;
    height: 1px;
    margin: 10px 0;
  }
}

@mixin sidebar-list-variant($text-color) {
  @extend .sidebar-list;
  #sidebar-wrapper ul & > a {
    color: $text-color;
  }
  #sidebar-wrapper ul & > a:hover {
    color: #fff;
    border-left: 4px solid $text-color;
  }
  #page-wrapper:not(.open) #sidebar-wrapper ul & > a:hover .menu-icon {
    border-left: 4px solid $text-color;
  }
}
